<template>
  <section class="wrap">
    <section class="wrap_in" :class="{ unscrollable: isUnscRollable }">
      <!-- 轮播图 -->
      <section class="banners">
        <div class="search_fun">
          <div class="s_left">阿康</div>
          <div class="s_center" @click="jumpSearch"><input type="text" readonly><i></i></div>
          <div class="s_right">
            <!-- <router-link v-if="!$store.state.memberId" to="/login">登录</router-link> -->
            <!-- 已经登录的时候显示 -->
            <!-- <router-link v-if="$store.state.memberId" class="isLogin" to="/myinfo"></router-link> -->
            <router-link v-if="!isSignIn" to="/login">登录</router-link>
            <!-- 已经登录的时候显示 -->
            <router-link v-if="isSignIn" class="isLogin" to="/myinfo"></router-link>
          </div>
        </div>
        <swiper
          :options="swiperOptionImg" 
          ref="mySwiper-img"
          class="swiperBanners">
          <swiper-slide v-for='(wbs,index) in wapBanners' :key="index">
            <a :href="wbs.advertisementUrl"><img :src="wbs.advertisementPhoto"></a>
          </swiper-slide>
        </swiper>
        <!-- 分页 -->
        <div class="banners_pagination swiper-pagination"></div>
      </section>
      <!-- 轮播图 end -->
      <!-- 咨询窗口 -->
      <section class="serviceBlock">
        <!-- <a href="http://vipwebchat.tq.cn/pageinfo.jsp?version=vip&admiuin=9815275&ltype=1&iscallback=0&page_templete_id=122543&is_message_sms=0&is_send_mail=0&action=acd&acd=1&type_code=123"></a> -->
        <a id="tqchat1" href="javascript:void(0)" title="在线咨询"></a>
      </section>
      <!-- 咨询窗口 end -->
      <!-- 主体 -->
      <main class="main">
        <!-- 导航列表 -->
        <section class="navLeft">
          <ul class="d_flex">
            <li>
              <router-link to="/sort">
                <img src="../../../static/images/navleft-qb.png" alt="">
                <p>全部分类</p>
              </router-link>
            </li>
            <li>
              <router-link to="/symptom">
                <img src="../../../static/images/navleft-zy.png" alt="">
                <p>找药</p>
              </router-link>
            </li>
            <li>
              <router-link to="/manb">
                <img src="../../../static/images/navleft-mb.png" alt="">
                <p>慢性病</p>
              </router-link>
            </li>
            <li>
              <router-link to="/c_male">
                <img src="../../../static/images/navleft-nx.png" alt="">
                <p>男性频道</p>
              </router-link>
            </li>
            <li>
              <router-link to="/c_female">
                <img src="../../../static/images/navleft-nr.png" alt="">
                <p>女性频道</p>
              </router-link>
            </li>
            <li>
              <router-link to="/crare">
                <img src="../../../static/images/navleft-zx.png" alt="">
                <p>罕见病</p>
              </a>
            </router-link>
            <li>
              <router-link to="/order">
                <img src="../../../static/images/navleft-dd.png" alt="">
                <p>我的订单</p>
              </router-link>
            </li>
            <li>
              <router-link to="/myinfo">
                <img src="../../../static/images/navleft-gr.png" alt="">
                <p>个人中心</p>
              </router-link>
            </li>
          </ul>
        </section>
        <!-- 导航列表 end -->
        <!-- 健康资讯 -->
        <section class="information">
          <div class="information_img inline_block"></div>
          <div class="information_liist inline_block">
            <ul :style="{ top }">
              <li 
                v-for="(al,index) in artLists"
                @click="jumpArticle(al,index)" 
                :class="index | artClassFilter">{{ al.networkTitle }}</li>
            </ul>
          </div>
          <div class="information_more inline_block">
            <router-link to="/article">更多</router-link>
          </div>
        </section>
        <!-- 健康资讯 end -->

        <!-- 阿康推荐 -->
        <section class="recommend" id="akRecommend">
          <div class="r_header"><h3>阿康推荐</h3><!-- <a href="javascript:;">更多精选</a> --></div>
          <div class="rec_con">
            <ul class="d_flex" v-if="akRecommend.length>0">
              <li v-for="(ard,index) in akRecommend" @click="jumpDetail(ard)">
                <div class="f_left">
                  <img :src="ard | photoFilter" :alt="ard.productName">
                </div>
                <div class="rc_info">
                  <h3>{{ ard.productName }}</h3>
                  <!-- <p>{{ ard.describe }}</p> -->
                  <h2 class="r_color">
                    <p class="f_left"><i>￥</i>{{ ard.ourPrice }}</p>
                    <p class="f_right"><span>￥{{ ard.price }}</span></p>
                  </h2>
                </div>
              </li>
            </ul>
          </div>
        </section>
        <!-- 阿康推荐 end -->
        <!-- 广告位-1 -->
        <section class="fadv fadv-1" id="floorAdv1">
          <mt-swipe :auto="2000" >
            <mt-swipe-item v-for="(fa,index) in floorAdv1" :key="index"><a :href="fa.advertisementUrl"><img :src="fa.advertisementPhoto"></a></mt-swipe-item>
          </mt-swipe>
        </section>
        <!-- 广告位-1 end -->
        <!-- 底部tab -->
        <section class="btab">
          <div class="tabs">
            <swiper :options="swiperOption" ref="mySwiper">
               <swiper-slide>
                <a :class="{cur: selected=='105'}" @click.stop="tabClick('105')" href="javascript:;"><span>个人推荐</span></a>
                </swiper-slide>
               <swiper-slide>
                <a :class="{cur: selected=='106'}" @click.stop="tabClick('106')" href="javascript:;"><span>慢性病</span></a>
                </swiper-slide>
               <swiper-slide>
                <a :class="{cur: selected=='107'}" @click.stop="tabClick('107')" href="javascript:;"><span>健康保健</span></a>
                </swiper-slide>
               <swiper-slide>
                <a :class="{cur: selected=='108'}" @click.stop="tabClick('108')" href="javascript:;"><span>男性频道</span></a>
                </swiper-slide>
               <swiper-slide>
                <a :class="{cur: selected=='109'}" @click.stop="tabClick('109')" href="javascript:;"><span>女性频道</span></a>
                </swiper-slide>
            </swiper>
          </div>
          <!-- tab-container -->
          <mt-tab-container v-model="selected">
            <mt-tab-container-item id="105">
              <ul class="tab_list" v-if="personalRecom.length>0">
                <li id="pr" v-for="pr in personalRecom" @click="jumpDetail(pr)">
                  <div class="tpro_img"><img :src="pr | photoFilter" :alt="pr.productName"></div>
                  <div class="tpro_info">
                    <h3>{{ pr.productName }}</h3>
                    <span>{{ pr.spec }}</span>
                    <h3 class="r_color">{{ pr.ourPrice | priceFilter }}</h3>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="106">
              <ul class="tab_list" v-if="manPage.length>0">
                <li id="mp" v-for="mp in manPage" @click="jumpDetail(mp)">
                  <div class="tpro_img"><img :src="mp | photoFilter" :alt="mp.productName"></div>
                  <div class="tpro_info">
                    <h3>{{ mp.productName }}</h3>
                    <span>{{ mp.spec }}</span>
                    <h3 class="r_color">{{ mp.ourPrice | priceFilter }}</h3>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="107">
              <ul class="tab_list" v-if="healthyPage.length>0">
                <li id="hp" v-for="hp in healthyPage" @click="jumpDetail(hp)">
                  <div class="tpro_img"><img :src="hp | photoFilter" :alt="hp.productName"></div>
                  <div class="tpro_info">
                    <h3>{{ hp.productName }}</h3>
                    <span>{{ hp.spec }}</span>
                    <h3 class="r_color">{{ hp.ourPrice | priceFilter }}</h3>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="108">
              <ul class="tab_list" v-if="malePage.length>0">
                <li id="mpe" v-for="mpe in malePage" @click="jumpDetail(mpe)">
                  <div class="tpro_img"><img :src="mpe | photoFilter" :alt="mpe.productName"></div>
                  <div class="tpro_info">
                    <h3>{{ mpe.productName }}</h3>
                    <span>{{ mpe.spec }}</span>
                    <h3 class="r_color">{{ mpe.ourPrice | priceFilter }}</h3>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="109">
              <ul class="tab_list" v-if="femalePage.length>0">
                <li id="fp" v-for="fp in femalePage" @click="jumpDetail(fp)">
                  <div class="tpro_img"><img :src="fp | photoFilter" :alt="fp.productName"></div>
                  <div class="tpro_info">
                    <h3>{{ fp.productName }}</h3>
                    <span>{{ fp.spec }}</span>
                    <h3 class="r_color">{{ fp.ourPrice | priceFilter }}</h3>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
          </mt-tab-container>
        </section>
        <!-- 底部tab end -->
        
        <!-- 底部 -->
        <com-foot></com-foot>
        <!-- 底部 end -->
      </main>
      <transition name="fade">
        <section 
          class="backTop"
          @click="backTop"
          v-show="showBackTop">
          <a href="javascript:;"></a>
        </section>
      </transition>
    </section>
    <!-- 子页面 -->
    <transition name="fade">
      <router-view v-on:changeClass="changeClassOne"></router-view>
    </transition>
    <!-- 子页面 end -->
  
    <!-- 主体 end -->
  </section>
</template>
<script>
import {Swipe, SwipeItem, Navbar, TabItem, TabContainer, TabContainerItem, Cell} from 'mint-ui'
import 'swiper/dist/css/swiper.css'  
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'mint-ui/lib/style.css'
import comFoot from '@/components/Common/ComFoot'

export default {
  name: 'Index',
  data () {
    return {
      //首页轮播
      swiperOptionImg: {
          autoplay: true,
          nested: true, //用于嵌套相同方向的swiper时，当切换到子swiper时停止父swiper的切换
          pagination: { //使用分页导航
            el: '.banners_pagination',
            clickable :true,
          }
      },
      isUnscRollable: false, //是否页面不可滚动
      backParentPage: false, //是否回到父页面
      selected: '105',
      swiperOption: {
         // notNextTick: true,
         autoplay: 0,
         slidesPerView: 4.5,
         setWrapperSize :true,
         slideToClickedSlide: true, //点击slide会过渡到这个slide。
      },
      windowHeight: '', //窗口高度
      showBackTop: false,// 是否显示返回顶部
      //轮播图模块
      wapBanners: '',
      //推荐文章
      artLists: '',
      artListsIndex: 0,//当前文章
      //阿康推荐模块
      akRecommend: [],
      //楼层广告模块1
      floorAdv1: '',
      //个人推荐模块
      personalRecom: [],
      //慢性病模块
      manPage: [],
      //健康保健模块
      healthyPage: [],
      //男性频道模块
      malePage: [],
      //女性频道模块
      femalePage: [],
    }
  },
  components: {
    'mt-swipe': Swipe,
    'mt-swipe-item': SwipeItem,
    'mt-navbar': Navbar,
    'mt-tab-item': TabItem,
    'mt-tab-container': TabContainer,
    'mt-tab-container-item': TabContainerItem,
    'mt-cell': Cell,
    swiper,
    swiperSlide,
    comFoot, // 公共底部
  },
  filters: {
    //文章Class
    artClassFilter ( index ){
      var curIndex = index+1;
      
      if( curIndex%2 == 0 ) {
        return 'newImg-2';
      }else{
        return 'newImg-1';
      }
    },
    //价格
    priceFilter ( data ){
      return '￥' + Number(data).toFixed(2);
    },
    //图片地址筛选
    photoFilter ( data ){
      if(data.productPhotos.length <= 0){
        return './static/images/ak.png';
      }else{
        return data.productPhotos[0].photoURL;
      }
    },
  },
  computed: {
    top() {
      return - this.artListsIndex * 0.5625 + 'rem';
    },
    //是否登录
    isSignIn (){
      if( this.$store.state.memberId ){
        return true;
      }else{
        return false;
      }
    },
  },
  created : function(){
    const _self = this;
    var myDate = new Date();//获取系统当前时间
    var y = myDate.getFullYear();
    var m = myDate.getMonth() + 1;
    if( m < 10 ){
      m = '0'+m;
    }
    var d = myDate.getDate();
    if( d < 10 ){
      d = '0'+d;
    }
    var datas = y + '-' + m + '-' + d + ' ' + '23:59:59';
    // console.log('datas: ', datas)

    //轮播图模块
    this.$ajax({
      url: _self.webRoot + '/seachAd',
      // url: '/seachAd',
      data: {
        endDate: datas,
        pageNo: 'wapBanners',
        isValid: 1
      }
    })
    .then(function(res){
      // console.log('轮播图：',res)
      if( res.data.success ){
        _self.wapBanners = res.data.data;
      }else{
        _self.wapBanners = '';
        console.log('轮播图模块不存在')
      }
    })
    .catch(function(err){
      console.log(err)
    });

    //健康咨询
    this.$ajax({
      url: _self.webRoot + '/healthAdvisoryType/getHealthAdvisoryTypeList'
    })
    .then(function(res){
      // console.log('文章：', res)
      if( res.data.success ){
        if( res.data.data.length <= 0 ){
          // _self.$options.methods.showToast('暂无文章！');
          console.log('文章模块无数据');
          return false;
        }
        _self.artLists = res.data.data[0].healthAdvisoryList; //推荐文章
        //增加 healthAdvisoryTypeName 一级分类名
        for( var i = 0; i < res.data.data[0].healthAdvisoryList.length; i++ ){
          res.data.data[0].healthAdvisoryList[i].healthAdvisoryTypeName = res.data.data[0].healthAdvisoryTypeName;
        }
        setInterval(function(){
          if(_self.artListsIndex < _self.artLists.length-2) {
            _self.artListsIndex += 1;
          } else {
            _self.artListsIndex = 0;
          }
        },3000)
      }else{
        // _self.$options.methods.showToast('获取数据失败！');
        console.log('文章模块无数据');
      }
    })
    .catch(function(err){
      console.log(err)
    });

    //阿康推荐模块
    this.$options.methods.getProRecom( _self, datas, 'akRecommend', datas, _self.akRecommend, '阿康推荐模块无数据' );

    //楼层广告模块1
    this.$ajax({
      url: _self.webRoot + '/seachAd',
      // url: '/seachAd',
      data: {
        endDate: datas,
        pageNo: 'floorAdv1',
        isValid: 1
      }
    })
    .then(function(res){
      // console.log('floorAdv1: ',res)
      if( res.data.success ){
        _self.floorAdv1 = res.data.data;
      }else{
        _self.floorAdv1 = '';
        console.log('楼层广告模块1不存在')
      }
    })
    .catch(function(err){
      console.log(err)
    });

    // return false;
    //个人推荐
    this.$options.methods.getProRecom( _self, datas, 'personalRecom', datas, _self.personalRecom, '个人推荐模块无数据' );
    //慢性病
    this.$options.methods.getProRecom( _self, datas, 'manPage', datas, _self.manPage, '慢病模块无数据' );
    //健康保健
    this.$options.methods.getProRecom( _self, datas, 'healthyPage', datas, _self.healthyPage, '健康保健模块无数据' );
    //男性频道
    this.$options.methods.getProRecom( _self, datas, 'malePage', datas, _self.malePage, '男性频道模块无数据' );
    //女性频道
    this.$options.methods.getProRecom( _self, datas, 'femalePage', datas, _self.femalePage, '女性频道模块无数据' );
  },
  mounted () {
    const _self = this;

    //获取窗口高度
    this.windowHeight = document.documentElement.clientHeight;
    // console.log(document.documentElement.clientHeight)
    //监听滚动
    window.addEventListener('scroll', this.handleScroll);
  },
  updated (){
    this.$nextTick(function () {
      // if( !this.backParentPage ){
      //   this.isUnscRollable = true;
      // }
    })  
  },
  destroyed (){

  },
  methods: {
    // 跳到商品详情页
    jumpDetail ( item ){
      // console.log(item)
      // return false
      this.$router.push({ path: 'product_detail', query: {proid: item.productID} });
    },
    //切换tab
    tabClick(index){ 
      const _self = this;

      this.selected = index;
    },
    //跳转到通用搜索页
    jumpSearch (){
      this.$router.push({ path:'search', query:{id: '002'} });
    },
    handleScroll: function(){
      //滚动高度
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // console.log( scrollTop > this.windowHeight/4 )

      if( scrollTop > this.windowHeight/4 ){
        this.showBackTop = true;
      }else {
        this.showBackTop = false;
      }
    },
    //跳转文章详情
    jumpArticle (al,index){
      // console.log(al.healthAdvisoryTypeName)
      this.$router.push({ path: '/article/detail', query: {typeName: al.healthAdvisoryTypeName, aid: al.healthAdvisoryID } });
    },

    // 共用函数
    // 判断是否返回父页面
    changeClassOne ( isChildDestroyed ){
      if( isChildDestroyed ){ 
        //退出子页面
        this.backParentPage = true; //返回首页
        this.isUnscRollable = false;
      }else{
        //没退出子页面
        this.backParentPage = false; // 没返回首页
        this.isUnscRollable = true;
      }
    },
    // 获取模块数据
    getProRecom ( _self, endDate1, pageNo, endDate2, moduleData, errMsg ){
      _self.$ajax({
        url: _self.webRoot + '/seachSt',
        // url: '/seachSt',
        data: {
          endDate: endDate1,
          pageNo: pageNo,
          isValid: 1
        }
      })
      .then(function(res){
        if(res.data.data.length <= 0){return}
        var showId = res.data.data[0].showID;
        // console.log('getProRecom: ', res)
        if( res.data.success ){
          _self.$ajax({
            url: _self.webRoot + '/seachStDetail',
            data: {
              endDate: endDate2,
              showID: showId,
              isValid: 1
            }
          })
          .then(function(res){
            // console.log('seachStDetail: ', res)
            if( res.data.success ){
              moduleData.splice(0); //先移除数据
              
              var productIdList = [];

              for( let x in res.data.data ){
                productIdList.push( res.data.data[x].productID );
              }

              _self.$ajax({
                url: _self.webRoot + '/product/getProductByProductNumber',
                data: JSON.stringify({
                  memberId: _self.$store.state.memberId,
                  productNumbers: productIdList,
                }),
                headers: {'Content-Type' : 'application/json;charset=utf-8'},
                transformRequest: [function (data) {
                    return data
                }]
              })
              .then(function(res){
                // console.log('productNumbers: ', res)
                if( res.data.success && res.data.data.length>0 ){
                  for( let x in res.data.data ){
                    moduleData.push(res.data.data[x])
                  }
                }
                if( !res.data.success ){
                  moduleData = [];
                  console.log(errMsg)
                }
                // console.log('akRecommend', moduleData)
              })
              .catch(function(err){
                console.log(err)
              });
            }else{
              moduleData = [];
              console.log(errMsg)
            }
          })
          .catch(function(err){
            console.log(err)
          });
        }else{
          moduleData = [];
          console.log(errMsg)
        }
      })
      .catch(function(err){
        console.log(err)
      });
    },
    // 返回顶部
    backTop (){
      var timer = null;
      
      // cancelAnimationFrame(timer);
      // timer = requestAnimationFrame(function fn(){
      //   var oTop = document.body.scrollTop || document.documentElement.scrollTop;
        
      //   if(oTop > 0){
      //     document.body.scrollTop = document.documentElement.scrollTop = oTop - 1;
      //     timer = requestAnimationFrame(fn());
      //   }else{
      //     cancelAnimationFrame(timer);
      //   } 
      // });
      
      // clearTimeout(timer);
      // timer = setTimeout(function fn(){
      //   var oTop = document.body.scrollTop || document.documentElement.scrollTop;
      //   if(oTop > 0){
      //     document.body.scrollTop = document.documentElement.scrollTop = oTop - 1;
      //     timer = setTimeout(fn());
      //   }else{
      //     clearTimeout(timer);
      //   } 
      // });
      
      // var set = setInterval(function(){
      //     var toTop = document.documentElement.scrollTop||document.body.scrollTop;
      //     if(toTop<=0)
      //         clearInterval(set);
      //     document.documentElement.scrollTop=toTop-30;
      // },1);
      
      cancelAnimationFrame(timer);
      timer = requestAnimationFrame(function fn(){
        var oTop = document.body.scrollTop || document.documentElement.scrollTop;
        
        if(oTop > 0){
          document.body.scrollTop = document.documentElement.scrollTop = oTop - 1;
          timer = requestAnimationFrame(fn());
        }else{
          cancelAnimationFrame(timer);
        } 
      });
    },
  },
}
</script>


<style >
@import './Css/Index.css';
</style>
<style scoped> 
.wrap_in {
  padding-top: 0;
}
</style>



